.success__container {
  display: grid;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.success__container svg {
  color: #0fa958;
  font-size: 300px;
  margin-top: 40%;
}

.success__container h6 {
  text-align: center;
  font-size: 14px;
  margin-top: 5%;
}

.success__container button {
  padding: 1.3rem;
  width: 60%;
  background-color: #ffcc00;
  color: black;
  border: none;
  box-shadow: 0px 2px 15px rgba(255, 204, 0, 0.35);
  border-radius: 10px;
  margin-top: 20px;
  cursor: pointer;
  margin-left: 65px;
}
