@import url("https://fonts.googleapis.com/css2?family=Gothic+A1&display=swap");
@font-face {
  font-family: "MTNBrighterSans-Medium";
  src: url("./assets/MTNBrighterSans-Medium.eot");
  src: url("./assets/MTNBrighterSans-Medium.woff") format("woff");
}
@import url("./modules/shared/css/home.css");
@import url("./modules/shared/css/button.css");
@import url("./modules/shared/css/input.css");
@import url("./modules/shared/css/404.css");
@import url("./modules/shared/css/dashboard.css");

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: MTNBrighterSans-Medium, "Gothic A1";
}

.appContainer {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100vh;
  justify-content: space-around;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.8;

  height: 100vh;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.pageContents {
  display: flex;
  flex-direction: column;
  padding: 0.5rem 2rem;
  /* min-height: 50vh; */
}

/* Media Query */

@media screen and (min-width: 1300px) and (max-width: 1600px) {
}

@media screen and (max-width: 1300px) {
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
  .contentsRight {
    flex: 1;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .appContainer {
    flex-direction: column;
  }

  .contentsRight {
    flex: 1;
  }
}

@media screen and (max-width: 700px) {
  .appContainer {
    flex-direction: column;
  }

  .contentsRight {
    flex: 1;
  }
}

@media screen and (max-width: 550px) {
  .appContainer {
    flex-direction: column;
  }

  .contentsRight {
    flex: 1;
  }
}
