.home {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
}
.imageBackground {
  display: flex;
  flex: 0.65;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3)),
    url(../../../assets/bg1.png);
  background-size: cover;
  background-repeat: no-repeat;
  /* background-position: center center; */
  animation: slideBg 20s linear infinite 8s;
  animation-timing-function: ease-in-out;
  padding: 3rem;
  color: white;
}
.imageBackground h1 {
  margin-top: 2rem;
  font-size: 4rem;
}
.imageBackground h3 {
  font-size: 2rem;
}

@keyframes slideBg {
  0% {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3)),
      url(../../../assets/bg1.png);
    background-size: cover;
  }

  25% {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3)),
      url(../../../assets/bg2.jpg);
    background-size: cover;
  }

  50% {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3)),
      url(../../../assets/bg1.png);
    background-size: cover;
  }

  75% {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3)),
      url(../../../assets/bg2.jpg);
    background-size: cover;
  }

  100% {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3)),
      url(../../../assets/bg1.png);
    background-size: cover;
  }
}
.imageBackgroundLogo {
  width: 100%;
  height: 5rem;
  overflow: hidden;
  padding: 0.5rem;
}
.imageBackgroundLogo img {
  object-fit: contain;
  max-height: 4rem;
}
.formContainer {
  display: flex;
  flex: 0.35;
  flex-direction: column;

  padding: 1rem;
}
.formPadding {
  padding: 0.5rem 2rem;
  margin-bottom: 5rem;
}
.formPadding h1 {
  font-size: 4rem;
}
.input {
  border-radius: 0 !important;
  border: none !important;
  border-bottom: 2px solid rgba(0, 0, 0, 0.3) !important;
  height: 2.5rem !important;
  margin-bottom: 1rem !important;
  padding: 0 !important;
  /* background: white !important; */
}
.input:focus {
  outline: none !important;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

