@media screen and (max-width: 1300px) {
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
}

@media screen and (max-width: 1050px) {
  .imageBackground h1 {
    margin-top: 1rem;
    font-size: 3rem;
  }
  .imageBackground h3 {
    font-size: 1.5rem;
  }
  .formPadding {
    margin-bottom: 3rem;
  }
  .formPadding h1 {
    font-size: 3rem;
  }
}

@media screen and (max-width: 1024px) {
}

@media screen and (min-width: 800px) and (max-width: 1024px) {
}

@media screen and (min-width: 700px) and (max-width: 800px) {
  .imageBackground {
    flex: 0.5;
  }
  .formContainer {
    padding: 0;
    flex: 0.5;
  }
  .formPadding p {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 700px) {
  .home {
    flex-direction: column;
  }
  .imageBackground {
    background-position: top center;
    background-size: cover;
    height: 70vh;
  }
  .formContainer {
    padding: 1rem 6rem;
    height: fit-content;
  }
  .formPadding {
    text-align: center;
  }
}

@media screen and (max-width: 550px) {
  .imageBackground {
    height: 50vh;
    padding: 2rem;
  }
  .imageBackground h1 {
    font-size: 2rem;
  }
  .imageBackground h3 {
    font-size: 1rem;
  }
  .formContainer {
    padding: 0rem 1rem;
    height: fit-content;
  }
  .formPadding {
    margin-bottom: 1rem;
  }
  .formPadding h1 {
    font-size: 1.5rem;
  }
  .formPadding p {
    font-size: 0.7rem;
  }
  .input {
    height: 2rem !important;
    margin-bottom: 0rem !important;
  }
}
