.result__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 3rem;
    height: 50vh;
    width: 100%;
}


.result_message{
    display: flex;
    flex-direction: column;
    align-items: center;
   
}
.result_message h2 {
    display: flex;
    margin-bottom: 3rem;
}
.result_message img{
    display: flex;
}

.result__container h3 {
    margin-top: 2rem;
    margin-bottom: 2rem;
}