.instructions {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.instructions h3 {
  margin-bottom: 1rem;
}
.instructions p {
  margin-bottom: 1rem;
}
.instructions ul li {
  line-height: 2rem;
}
.modalTitle {
  display: flex;
  padding: 0.5rem 2rem;
  margin-bottom: 0.5rem;
  font-size: 16px;
}
/* table */
table {
  width: 100%;
  table-layout: fixed;
}
.tbl-header {
  background-color: #ffcc00;
}
.tbl-content {
  height: 300px;
  overflow-x: auto;
  margin-top: 0px;
  border: 1px solid rgba(255, 255, 255, 0.3);
}
th {
  padding: 20px 15px;
  text-align: left;
  font-weight: 500;
  font-size: 12px;
  color: black;
  text-transform: uppercase;
}
td {
  padding: 15px;
  text-align: left;
  vertical-align: middle;
  font-weight: 300;
  font-size: 12px;
  color: black;
  border-bottom: solid 1px rgba(255, 255, 255, 0.1);
}

.options {
  margin-bottom: 1rem;
}

/* exam */

.timerContainer {
  display: flex;
  justify-content: flex-end;
}
.testTimer {
  display: flex;
  padding: 1rem 3rem;
  border: 1px solid #ffcc00;
  border-radius: 10px;
  font-size: 2rem;
  color: #ffcc00;
}
.circleContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}
.circle {
  display: flex;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  color: black;
  font-size: 2rem;
  background: #ffcc00;
  justify-content: center;
  align-items: center;
}
.center {
  justify-content: center;
}
