.mtn__navigation {
  display: flex;
  flex: 0.2;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  padding: 1.5rem 0.6rem;
  background: #ffffff;
}

.mtn__logo {
  display: flex;
  padding: 0.3rem;
  height: 2rem;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}

.mtn__logo img {
  object-fit: contain;
  height: 100%;
  width: 100%;
}

.mtn__oval_header {
  display: flex;
  padding: 0.3rem;
  height: 3.2rem;
  margin-top: 1.5rem;
  justify-content: center;
  align-items: center;
}
.image_container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  border: 1px solid #ffcc00;
  padding: 2%;
  background-color: #fff;
}
.image_container img {
  display: flex;
  height: 100%;
  width: 100%;
  border-radius: 60%;
  object-fit: cover;
  /* max-height: 4rem; */
}
.mtn__url {
  display: flex;
  padding: 1rem 1.5rem;
  padding-top: 2rem;
  flex-direction: column;
}

 ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 100%;
  flex-direction: column;
}

 ul li {
  display: flex;
  list-style: none;
  justify-content: space-between;
  height: 3rem;
  cursor: pointer;
  padding-left: 1rem;
  align-items: center;
  margin-bottom: 1.5rem;
  flex-direction: row;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}
ul li svg {
  margin-top: 16px;
  font-size: 18px;
  margin-left: 10px;
  position: relative;
  top: 4px;
  color: rgba(255, 204, 0, 1);
}
ul li a {
  display: flex;
  list-style: none;
  justify-content: space-between;
  text-decoration: none;
  color: black;
}
ul li span {
  display: flex;
  list-style: none;
  justify-content: space-between;
}

ul li svg {
  margin-right: 0.5rem;
  margin-bottom: 0.4rem;
}

.mtn__btn {
  width: 100%;
  height: 3rem;
  outline: none;
  padding: 0.8rem 3rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  background: rgba(30, 30, 30, 1);

  text-decoration: none;
  border: none;
  outline: none;
  background: rgba(255, 204, 0, 1);
  align-items: center;
}

.mtn__btn[disabled] {
  background: rgba(94, 91, 91, 0.14);
}


/* phone */
.mtn__navigation_phone {
  display: flex;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  padding: 1rem 0.6rem;
  position:absolute;
  background: #ffffff;
  top: 0;
  transition: 3s;
  width: 100%;
}

.mtn__logo_phone {
  display: flex;
  padding: 0.3rem;
  height: 2rem;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}

.mtn__logo_phone img {
  object-fit: contain;
  height: 100%;
  width: 100%;
}

.mtn__oval_header_phone {
  display: flex;
  padding: 0.3rem;
  height: 3.2rem;
  margin-top: 1.5rem;
  justify-content: center;
  align-items: center;
}

.mtn__url_phone {
  display: flex;
  padding: 1rem 1.5rem;
  padding-top: 2rem;
  flex-direction: column;
}

.mtn__url_phone ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 100%;
  flex-direction: column;
}

.mtn__btn_phone {
  width: 100%;
  height: 3rem;
  outline: none;
  padding: 0.8rem 3rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 12px;
  font-weight: bold;
  background: rgba(30, 30, 30, 1);

  text-decoration: none;
  border: none;
  outline: none;
  background: rgba(255, 204, 0, 1);
  align-items: center;
}

.cancel__btn{
  display: flex;
  justify-content: flex-end;
  margin-top: .5rem;
  margin-right: 1rem;
  font-size: 28px;
  color: rgba(255, 204, 0, 1);
}

.profile__btn{
  display: none;
  font-size: 22px;
  color: rgba(255, 204, 0, 1);
}
/* phone nav ends */


