    transition: 3s;
    width: 60%;
  }

  .mtn__navigation {
    display: flex;
    flex:0.10;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
      padding: .2rem 1rem;
  }
  .mtn__oval_header {
    display: none;
  }
.mtn__url{
