.sliderContainer {
  display: flex;
  flex-direction: column;
}
.slider {
  width: 100%;
  height: 1rem;
  background: whitesmoke;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.indicator {
  height: 1rem;
  background: #ffcc00;
  border-radius: 10px;
}

.sliderContainer p {
  font-size: 11px;
  text-align: center;
  margin-bottom: 0.5rem;
}
