.header__container {
  height: 70px;
  padding: 0.5rem 1.2rem;
  background-color: rgb(246, 210, 9);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header__title h2 {
  font-size: 25px;
  font-weight: bold;
  margin-left: 10px;
  /* color: white; */
  
}

.header__title {
  display: flex;
  color: black;
  justify-content: space-between;
}

.header__user {
  font-size: 15px;
  margin-right: 30px;
  font-weight: bold;
  margin-bottom: 5px;
}

.nameContainer {
  display: flex;
  align-items: center;
}

.nameContainer button {
  cursor: pointer;
}

.header__title svg {
  font-size: 20px;
  margin-right: 20px;
  cursor: pointer;
  font-weight: bold;
}

.logOut_icon{
  display: none;
}

