.home404{
  height: 100vh;
  width: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),
    url(../../../assets/bg1.png);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
}
.notFound{
  background: #ffcc00;
  padding: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
}
.notFound .title{
  font-size: 2rem;
}
.link{
  text-decoration: none;
}
.notFound .btn{
  padding: 1rem;
  font-size: 0.7rem;
  cursor: pointer;
}
.notFound .btn:hover{
  background-color: #020100;
  color: white;
}

@media screen and (max-width: 700px) {
  .home404{
    gap: 1rem;
  }
  .home404 img{
    width: 7rem;
  }
  .notFound{
    padding: 1rem;
  }
  .notFound .title{
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 550px) {
  .notFound .title{
    font-size: 1rem;
  }
}
